html {
  --pst-color-primary: #9f9fdf;
  --pst-color-primary-highlight: #202060;
  --pst-color-primary-bg: #12123b;
  --pst-color-secondary: #00ffa9;
  --pst-color-secondary-highlight: #b3ffe5;
  --pst-color-secondary-bg: #004d35;
  --pst-color-inline-code-links: #00ffa9;
  --pst-color-link: var(--pst-color-primary);
  --pst-color-link-hover: var(--pst-color-secondary);
  --pst-color-accent: #ff00db;
  --pst-color-inline-code: var(--pst-teal-200);
  --sd-color-shadow: var(--pst-color-secondary);
  --pst-color-rgba-docker: #2496ed;
  --pst-color-rgba-gitlab: #fc6d26;
  --pst-color-rgba-github: #4078c0;
  --pst-color-rgba-mattermost: #0058cc;
  --pst-color-rgba-numpy: #013243;
  --pst-color-rgba-pandas: #150458;
  --pst-color-rgba-poetry: #60a5fa;
  --pst-color-rgba-pre-commit: #fab040;
  --pst-color-rgba-pydantic: #e92063;
  --pst-color-rgba-pytest: #0a9edc;
  --pst-color-rgba-pytorch: #ee4c2c;
  --pst-color-rgba-ray: #028cf0;
  --pst-color-rgba-ruff: #d7ff64;
  --pst-color-rgba-scipy: #8caae6;
  --pst-color-rgba-sematic-release: #494949;
  --pst-color-rgba-tensorflow: #ff6f00;
  --pst-color-rgba-uv: #de5fe9;
  --pst-color-rgba-vs-code: #007acc;
  --spht-color-dark: var(--pst-color-on-background);
  --pst-color-dracula-bg: #282a36;
  --sd-color-card-border-hover: #526cfe;
  --pagefind-ui-background: var(--pst-color-on-background);
  --pagefind-ui-text: var(--pst-color-dark-text);
}
html[data-theme=dark] {
  --pst-color-primary: #9f9fdf;
  --pst-color-primary-highlight: #202060;
  --pst-color-primary-bg: #12123b;
  --pst-color-secondary: #00ffa9;
  --pst-color-secondary-highlight: #b3ffe5;
  --pst-color-secondary-bg: #004d35;
  --pst-color-inline-code-links: #00ffa9;
  --pst-color-link: var(--pst-color-primary);
  --pst-color-link-hover: var(--pst-color-secondary);
  --sd-color-shadow: var(--pst-color-secondary);
  --sd-color-secondary-highlight: #80006e;
  --pst-color-accent: #ff00db;
  --pst-color-inline-code: var(--pst-teal-200);
  --pst-color-rgba-numpy: #0385b0;
  --pst-color-rgba-pandas: #8d71f7;
  --pst-color-semantic-release: #999999;
  --spht-color-dark: var(--pst-color-on-background);
  --pagefind-ui-background: var(--pst-color-on-background);
  --pagefind-ui-text: var(--pst-color-dark-text);
}
html[data-theme=light] {
  --pst-color-primary: #9f9fdf;
  --pst-color-primary-highlight: #12123b;
  --pst-color-primary-bg: #202060;
  --pst-color-secondary: #80006e;
  --pst-color-secondary-highlight: #ffccf8;
  --pst-color-secondary-bg: #4d0042;
  --pst-color-inline-code-links: #80006e;
  --pst-color-link: var(--pst-color--secondary);
  --pst-color-link-hover: var(--pst-color-secondary-bg);
  --pst-color-accent: #ff00db;
  --pst-color-background: var(--pst-gray-50);
  --pst-color-surface: #f0f1f5;
  --pst-color-inline-code: var(--pst-pink-500);
  --sd-color-shadow: var(--pst-color-secondary);
  --sd-color-secondary-highlight: #4040bf;
}

button[id=search-button] {
  background-color: var(--pst-color-on-background);
  border-color: var(--pst-color-primary);
  width: 36px;
}
button[id=search-button]:hover > i {
  color: var(--pst-color-secondary);
}
button[id=search-button] > i {
  color: var(--pst-color-text-base);
}

.sidebar-primary-item button[id=search-button] {
  width: -webkit-fill-available;
}

button.cta-button:hover {
  color: var(--pst-color-link-hover);
  filter: drop-shadow(0px 0px 8px var(--pst-color-secondary));
}

button.theme-switch-button {
  border-width: 3;
}
button.theme-switch-button:hover .theme-switch.nav-link {
  box-shadow: none;
}
button.theme-switch-button:hover .theme-switch.nav-link svg path {
  color: var(--pst-color-link-hover);
}

button#backtotop.btn.rounded-pill {
  top: 85vh;
  left: 50vw;
  display: flex;
  column-gap: 0.5em;
  align-items: center;
  padding: var(--bs-btn-padding-y) var(--bs-btn-padding-x);
  bottom: unset;
  width: fit-content;
  color: var(--pst-color-secondary-text);
  background-color: var(--pst-color-secondary);
}
button#backtotop.btn.rounded-pill svg {
  transition: none;
}
button#backtotop.btn.rounded-pill.visible {
  transform: translate(0, -50%);
}
button#backtotop.btn.rounded-pill:hover {
  color: var(--pst-color-secondary);
  background-color: var(--pst-color-accent-bg);
  border-color: var(--pst-color-accent);
}

html[data-theme=light] .bd-header button.theme-switch-button:hover .theme-switch.nav-link svg path {
  color: var(--pst-color-primary);
}

html {
  --transition-very-fast: 0.25s;
  --transition-fast: 0.5s;
  --transition-med: 1s;
  --transition-slow: 2s;
  --transition-very-slow: 4s;
}
html a:not(.sd-button):not(.nav-link):not(.bd-side-nav):not(.sd-octicon),
html button:not(.nav-link):not(.bd-side-nav):not(.sd-octicon),
html kbd:not(.nav-link):not(.bd-side-nav):not(.sd-octicon),
html svg:not(.nav-link):not(.bd-side-nav):not(.sd-octicon),
html table:not(.nav-link):not(.bd-side-nav):not(.sd-octicon),
html ul:not(.nav-link):not(.bd-side-nav):not(.sd-octicon) {
  transition: all var(--transition-fast);
  -moz-transition: all var(--transition-fast);
  -webkit-transition: all var(--transition-fast);
}
html .sidebar-primary-item a:not(.sd-button).nav-link,
html .sidebar-secondary-item a:not(.sd-button).nav-link {
  transition: all var(--transition-fast);
  -moz-transition: all var(--transition-fast);
  -webkit-transition: all var(--transition-fast);
}
html .sidebar-primary-item a:not(.sd-button).nav-link svg,
html .sidebar-secondary-item a:not(.sd-button).nav-link svg {
  transition: none;
}
html button#search-button {
  transition: all var(--transition-fast);
  -moz-transition: all var(--transition-fast);
  -webkit-transition: all var(--transition-fast);
}

html .card {
  background-color: var(--pst-color-background);
}
html .card img {
  background-color: var(--pst-color-background);
}
html div.sd-container-fluid.sd-sphinx-override.sd-mb-4.sd-text-center.docutils > div {
  max-width: 50rem;
  margin: auto;
}
html .bd-content .sd-card .sd-card-header,
html .bd-content .sd-card .sd-card-body,
html .bd-content .sd-card .sd-card-footer {
  background-color: inherit;
}
html .sd-card.sd-main-index-card {
  transition: all var(--transition-fast);
  -moz-transition: all var(--transition-fast);
  -webkit-transition: all var(--transition-fast);
  padding: 30px 10px 20px 10px;
  border: 1px solid transparent;
  border-radius: 1em;
  max-width: 25rem;
}
html .sd-card.sd-main-index-card:hover {
  border: 1px solid var(--sd-color-card-border-hover);
}
html .sd-card.sd-main-index-card:hover .sd-btn-secondary {
  color: #ffffff !important;
}
html .sd-card.sd-main-index-card .sd-card-header .sd-card-text {
  margin: 0px;
}
html .sd-card.sd-main-index-card .sd-card-img-top {
  height: 52px;
  width: 52px;
  background: none !important;
  margin-left: auto;
  margin-right: auto;
  color: var(--pst-color-secondary);
}
html .sd-card.sd-main-index-card .sd-card-title {
  color: #9f9fdf;
  font-size: var(--pst-font-size-h5);
  padding: 1rem 0rem 0.5rem 0rem;
}
html .sd-card.sd-main-index-card .sd-card-footer {
  border: none !important;
}
html .sd-card.sd-main-index-card .sd-btn-secondary {
  background-color: #727c83 !important;
  border-color: #727c83 !important;
  border-radius: 4em;
}
html .sd-card.sd-main-index-card .sd-btn-secondary:hover {
  background-color: #6c757d !important;
  border-color: #9f9fdf !important;
  box-shadow: 0rem 0rem 0.75rem #9f9fdf !important;
}
html .sd-card.sd-main-index-card .sd-card-body {
  display: flex;
  flex-direction: column;
}
html .sd-card.sd-main-index-card .sd-card-body p.sd-card-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-grow: 1;
  margin-left: auto;
  margin-right: auto;
  border-image-source: linear-gradient(to right, rgba(0, 0, 0, 0), var(--pst-color-secondary), var(--pst-color-primary), var(--pst-color-secondary), rgba(0, 0, 0, 0));
  border-image-slice: 1 0 1;
  border-style: solid;
  border-width: 3px 0 3px;
}
html .sd-card.sd-main-index-card.sd-shadow-sm {
  box-shadow: 0 0 0.5rem #00ffa9 !important;
}
html .sd-card.sd-main-index-card.sd-shadow-sm:hover {
  box-shadow: 0 0 1rem #00ffa9 !important;
}
html .sd-card.sd-main-index-card.sd-shadow-md {
  box-shadow: 0 0 1rem #00ffa9 !important;
}
html .sd-card.sd-main-index-card.sd-shadow-md:hover {
  box-shadow: 0 0 1.5rem #00ffa9 !important;
}
html .sd-card.sd-main-index-card.sd-shadow-lg {
  box-shadow: 0 0 1.5rem #00ffa9 !important;
}
html .sd-card.sd-main-index-card.sd-shadow-lg:hover {
  box-shadow: 0 0 2rem #00ffa9 !important;
}
html[data-theme=light] .sd-card.sd-shadow-sm {
  box-shadow: 0 0 0.5rem #4040bf !important;
}
html[data-theme=light] .sd-card.sd-shadow-sm:hover {
  box-shadow: 0 0 1rem #4040bf !important;
}
html[data-theme=light] .sd-card.sd-shadow-md {
  box-shadow: 0 0 1rem #4040bf !important;
}
