@use "_colors.scss";

@mixin highlight() {
    td.linenos .normal {
        color: #f1fa8c;
        background-color: #44475a;
        padding-left: 5px;
        padding-right: 5px;
    }
    span.linenos {
        color: #f1fa8c;
        background-color: #44475a;
        padding-left: 5px;
        padding-right: 5px;
    }
    td.linenos .special {
        color: #50fa7b;
        background-color: #6272a4;
        padding-left: 5px;
        padding-right: 5px;
    }
    span.linenos.special {
        color: #50fa7b;
        background-color: #6272a4;
        padding-left: 5px;
        padding-right: 5px;
    }
    .hll {
        background-color: #44475a;
    }
    .c {
        color: #6272a4;
    } /* Comment */
    .err {
        color: #f8f8f2;
    } /* Error */
    .g {
        color: #f8f8f2;
    } /* Generic */
    .k {
        color: #ff79c6;
    } /* Keyword */
    .l {
        color: #f8f8f2;
    } /* Literal */
    .n {
        color: #f8f8f2;
    } /* Name */
    .o {
        color: #ff79c6;
    } /* Operator */
    .x {
        color: #f8f8f2;
    } /* Other */
    .p {
        color: #f8f8f2;
    } /* Punctuation */
    .ch {
        color: #6272a4;
    } /* Comment.Hashbang */
    .cm {
        color: #6272a4;
    } /* Comment.Multiline */
    .cp {
        color: #ff79c6;
    } /* Comment.Preproc */
    .cpf {
        color: #6272a4;
    } /* Comment.PreprocFile */
    .c1 {
        color: #6272a4;
    } /* Comment.Single */
    .cs {
        color: #6272a4;
    } /* Comment.Special */
    .gd {
        color: #8b080b;
    } /* Generic.Deleted */
    .ge {
        color: #f8f8f2;
